:root {
  --primaryBlue: hsl(208, 100%, 34%);
  --darkBlue: hsl(207, 100%, 13%);
  --medBlue: hsl(208, 57%, 47%);
  --lightBlue: hsl(195, 66%, 71%) ;
  --mutedBlue: hsl(208, 33%, 60%) ;
  --menuShadow: hsla(208, 33%, 60%) ;
  --primaryGreen: hsl(92, 51%, 51%);
  --darkGreen: hsl(92, 50%, 20%);
  --medGreen: hsl(92, 51%, 66%);
  --lightGreen:	hsl(92, 52%, 80%);
  --mutedGreen80: hsl(92, 37%, 71%, 80%);
  --primaryOrange: hsl(32, 94%, 60%);
  --darkOrange:	hsl(32, 62%, 38%);
  --medOrange:	hsl(33, 94%, 68%);
  --lightOrange: hsl(32, 95%, 84%);
  --mutedOrange: hsl(32, 61%, 76%);
  --primaryYellow: hsl(49, 100%, 50%);
  --darkYellow:	hsl(49, 100%, 30%);
  --medYellow: hsl(49, 100%, 65%);
  --lightYellow: hsl(49, 100%, 80%);
  --mutedYellow: hsl(48, 54%, 70%);
  --primaryRed: hsl(347, 100%, 45%);
  --darkRed: hsl(347, 100%, 27%);
  --medRed:	hsl(347, 83%, 56%);
  --lightRed: hsl(347, 82%, 78%);
  --mutedRed: hsl(347, 54%, 67%);
  --advocacyRed: hsl(353, 100%, 18%);
  --weFinancial: hsl(193, 100%, 42%);
  --weFinancialDark: hsl(207, 100%, 26%);
  --header-bg: hsl(45, 22%, 96%);
  --menu-overlay: hsla(207, 100%, 13%, 40%);
  --lightGrey: hsl(0, 0%, 81%);
  --medGrey: hsl(0, 0%, 53%);
  --dkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 6%);
  --textBlack: hsl(0, 0%, 16%);
  --white: hsl(45, 22%, 96%);
  --white60: hsla(240, 100%, 99%, .85);
  --yellow:	hsl(50, 99%, 70%);
  --amber: hsl(47, 100%, 48%);
}

:root {
  --font-heading: Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Atkinson Hyperlegible", Helvetica, Arial, sans-serif; /* body font */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  line-height: 1.1;
  letter-spacing: 0.7px;
  text-wrap: balance;
  margin: 0 0 1.25rem;
}

h1 {
  font-size: 2.5rem;
  font-size: clamp(2.5rem, 0.9375rem + 3.35vw, 3.5rem);
}

h2 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 1.6875rem + 0.78vw, 2.625rem);
  font-weight: 500;
}

h3 {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.1875rem + 0.26vw, 1.5rem);
  font-weight: 500;
  margin-bottom: 0;
}

.section-header {
  color: var(--primaryBlue);
  font-weight: 700;
}

p {
  font-size: 1rem;
  font-weight: 400;
  font-style: normal;
  text-wrap: balance;
  line-height: 1.5rem;
  margin-top: 0;
  margin-bottom: 2rem;
}

strong {
  color: var(--primaryBlue);
}

.large {
  font-size: 1.25rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.625rem;
}

.small {
  font-size: 0.75rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.5rem;
}

.quote {
  font-size: 1.125rem;
  line-height: 1.4;
  font-weight: 300;
  padding: 1.25rem;
}
.quote__promise {
  color: var(--darkBlue);
  background-color: var(--lightBlue);
  border-left: 12px solid var(--primaryBlue);
}

html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: var(--primaryBlue);
  text-decoration-thickness: 1px;
  text-underline-offset: 4px;
  cursor: pointer;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
  color: var(--black);
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
  shape-margin: 0.75rem;
}

figure {
  padding: 0;
}

figure,
blockquote {
  margin: 0;
}

.breadcrumb__section {
  margin-bottom: 5rem;
}
@media (max-width: 43.74875em) {
  .breadcrumb__section {
    margin-bottom: 1rem;
  }
}
.breadcrumb__active {
  color: var(--primaryBlue);
  font-weight: 700;
  letter-spacing: 1px;
}
.breadcrumb a {
  color: var(--darkBlue);
  text-decoration: underline;
}
.breadcrumb a:hover {
  color: var(--primaryOrange);
}

ul li {
  margin-bottom: 1rem;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

.btn {
  font-family: var(--font-text);
  font-size: 1.125rem;
  line-height: 1;
  font-weight: 400;
  text-align: center;
  border-radius: 8px;
  padding: 10px 32px 10px;
  margin: 0;
  text-decoration: none;
  display: inline-block;
}
.btn:hover {
  cursor: pointer;
}
.btn__advocacy {
  color: var(--darkBlue);
  background-color: var(--white);
  border: 1px solid var(--white);
}
.btn__advocacy:hover {
  color: var(--darkBlue);
  background-color: var(--primaryYellow);
  border: 1px solid var(--primaryYellow);
}
.btn__primary {
  color: var(--white);
  background-color: var(--darkBlue);
  border: 1px solid var(--darkBlue);
}
.btn__primary:hover {
  color: var(--darkBlue);
  background-color: var(--white);
  border: 1px solid var(--darkBlue);
}
.btn__secondary {
  color: var(--white);
  background-color: var(--primaryBlue);
  border: 1px solid var(--primaryBlue);
}
.btn__secondary:hover {
  color: var(--primaryBlue);
  background-color: var(--white);
}
.btn__tertiary {
  color: var(--white);
  background-color: var(--darkRed);
  border: 1px solid var(--darkRed);
}
.btn__tertiary:hover {
  color: var(--darkRed);
  background-color: var(--white);
}
.btn__we {
  color: var(--white);
  background-color: var(--weFinancialDark);
  border: 1px solid var(--weFinancialDark);
}
.btn__we:hover {
  color: var(--darkBlue);
  background-color: var(--white);
  border: 1px solid var(--weFinancial);
}
@media (max-width: 43.74875em) {
  .btn__primary, .btn__secondary, .btn__tertiary, .btn__advocacy, .btn__we {
    display: block;
  }
}
.btn__search {
  border: 0;
  background-color: var(--white);
}

.btn-contact-hhf {
  margin-left: 1.25rem;
  margin-bottom: 0.625rem;
}

